/* Global styles */
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #fff0f6;
  color: #333;
}

a {
  color: #ff4081;
  text-decoration: none;
}

nav {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 15px 0;
  background-color: #ffe6f0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

nav a {
  font-weight: bold;
  font-size: 1.1em;
}

.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  text-align: center;
}

h1 {
  color: #ff4081;
  font-size: 3em;
  margin-bottom: 20px;
}

button {
  background-color: #ff4081;
  border: none;
  color: white;
  padding: 12px 25px;
  font-size: 1em;
  cursor: pointer;
  border-radius: 8px;
  margin: 8px 0;
}

button:hover {
  background-color: #e0366d;
}
